}
/*定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸*/
::-webkit-scrollbar {
  width: .15rem;
  background-color: #d4eafa;
}
/*定义滚动条轨道 内阴影+圆角*/
::-webkit-scrollbar-track {
  width: .2rem;
  -webkit-box-shadow: inset 0 0 1px rgba(0, 158, 232, 0.3);
  background-color: #d4eafa;
}
/*定义滑块 内阴影+圆角*/
::-webkit-scrollbar-thumb {
  border-radius: 15px;
  border: .02rem solid #d4eafa;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 158, 232, 0.3);
  background-color: #009ee8;
}
